.about {
  color: #fafafa;
  margin-left: 40px;
  position: relative;
  font-size: 20px;
  line-height: 28px;
  font-family: 'Roboto Mono', monospace;
  margin-right: 40px; }
  .about .flex-wrapper {
    display: flex; }
    @media screen and (max-width: 1024px) {
      .about .flex-wrapper {
        flex-direction: column; } }
  .about .presentation-wrapper {
    position: relative; }
  .about .presentation {
    text-align: justify;
    position: relative; }
    .about .presentation h1 {
      margin-top: 0;
      opacity: 0;
      transform: translateY(-20px);
      will-change: opacity; }
      .about .presentation h1 span {
        text-decoration: line-through; }
    .about .presentation p {
      opacity: 0;
      will-change: opacity;
      transform: translateY(-20px); }
      .about .presentation p span {
        font-style: italic;
        opacity: 0.9;
        transition: opacity ease-out 0.2s; }
        .about .presentation p span:hover {
          opacity: 1; }
  .about .webGL-canvas {
    height: calc(100vh - 200px);
    position: relative;
    background: #050505;
    min-width: 600px;
    max-width: 600px;
    margin-right: 120px; }
    .about .webGL-canvas canvas {
      min-width: 100%;
      max-width: 100%;
      height: 100%; }
    @media screen and (max-width: 1024px) {
      .about .webGL-canvas {
        height: 400px;
        margin-bottom: 48px;
        min-width: 100%;
        max-width: 100%; } }
  .about a {
    color: #fafafa; }
