.audio-controller {
  padding: 8px; }
  .audio-controller .sound-wave {
    display: flex; }
  .audio-controller .sound-bar {
    width: 2px;
    height: 2px;
    background-color: #fafafa;
    margin-right: 2px;
    transform: scaleY(1);
    animation-duration: .5s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1); }
    .audio-controller .sound-bar:nth-child(4n) {
      animation-name: bar-scale-xl;
      animation-duration: 1s; }
    .audio-controller .sound-bar:nth-child(4) {
      animation-duration: 1.15s; }
    .audio-controller .sound-bar:nth-child(3) {
      animation-name: bar-scale-lg;
      animation-duration: 0.8s; }
    .audio-controller .sound-bar:nth-child(6) {
      animation-name: bar-scale-md;
      animation-duration: 0.85s; }
    .audio-controller .sound-bar:nth-child(2), .audio-controller .sound-bar:nth-child(5), .audio-controller .sound-bar:nth-child(7), .audio-controller .sound-bar:nth-child(9) {
      animation-name: bar-scale-sm;
      animation-duration: .9s; }

@keyframes bar-scale-sm {
  0%, 50% {
    transform: scaleY(1); }
  25% {
    transform: scaleY(6); }
  75% {
    transform: scaleY(4); } }

@keyframes bar-scale-md {
  0%, 50% {
    transform: scaleY(2); }
  25% {
    transform: scaleY(6); }
  75% {
    transform: scaleY(5); } }

@keyframes bar-scale-lg {
  0%, 50% {
    transform: scaleY(8); }
  25% {
    transform: scaleY(4); }
  75% {
    transform: scaleY(6); } }

@keyframes bar-scale-xl {
  0%, 50% {
    transform: scaleY(1); }
  25% {
    transform: scaleY(7); }
  75% {
    transform: scaleY(11); } }
  .audio-controller .sound-bar--still {
    animation: none; }
    .audio-controller .sound-bar--still:nth-child(4n) {
      transform: scaleY(7); }
    .audio-controller .sound-bar--still:nth-child(3) {
      transform: scaleY(4); }
    .audio-controller .sound-bar--still:nth-child(6) {
      transform: scaleY(6); }
    .audio-controller .sound-bar--still:nth-child(2), .audio-controller .sound-bar--still:nth-child(5), .audio-controller .sound-bar--still:nth-child(7), .audio-controller .sound-bar--still:nth-child(9) {
      animation-name: bar-scale-sm;
      animation-duration: .9s; }
