.social-networks {
  margin-top: 32px;
  margin-bottom: 8px; }
  .social-networks img {
    height: 24px; }
  .social-networks ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0; }
  .social-networks li {
    margin-left: 8px;
    margin-right: 8px; }
  .social-networks a {
    opacity: 0.6;
    transition: opacity 0.2s linear; }
    .social-networks a:hover {
      opacity: 1; }
