@import url("https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap");
html, body {
  box-sizing: border-box;
  background: #050505;
  color: #fafafa; }

.no-btn {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit; }

.layout {
  background: #050505;
  font-family: 'Roboto Mono', monospace; }

.target {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0.5;
  pointer-events: none; }

.target-icon {
  position: absolute;
  width: 40px; }

#resizeChecker {
  position: absolute;
  width: 5px;
  opacity: 0;
  display: block;
  pointer-events: none; }
  @media screen and (max-width: 1199px) {
    #resizeChecker {
      width: 4px; } }
  @media screen and (max-width: 768px) {
    #resizeChecker {
      width: 3px; } }
  @media screen and (max-width: 640px) {
    #resizeChecker {
      width: 2px; } }

.cta {
  border: #fafafa 1px solid;
  padding-left: 32px;
  padding-right: 32px;
  padding-top: 16px;
  padding-bottom: 16px;
  margin-top: 36px;
  opacity: 0.8;
  transition: opacity 0.2s linear; }
  .cta:hover {
    opacity: 1; }
